.profile__avatar {
    box-sizing: border-box;
    max-width: 120px;
    max-height: 120px;
    margin: 0;
    margin-right: 30px;
    padding: 0;
    position: relative;
}

@media screen and (min-width: 320px) and (max-width: 670px) {
    .profile__avatar {
        margin: 0 0 28px 1px;
    }
}